export default {
	onShareAppMessage(res) {
		return {
			title: '购物先领券，一年省一半',
			path: '/pages/index/index'
		}
	},
	data() {
		return {
			logo: "../../static/img/mao.png",
			taobao: "../../static/img/taobao.png",
			recentKeyword: [],
			tab: [{
					name: '热销',
					position: 1,
					total: 0,
					data: []
				},
				{
					name: '综合',
					position: 2,
					total: 0,
					data: []
				},
				{
					name: '最新',
					position: 3,
					total: 0,
					data: []
				},
				{
					name: '高佣',
					position: 4,
					total: 0,
					data: []
				},
				{
					name: '低价',
					position: 5,
					total: 0,
					data: []
				},
			],
			TabCur: 0,
			scrollLeft: 0,
			scrollTop: false,
			couponlist: [],
			page: 1,
			localKeyword: 'orange-tyh-keyword',
			position: 1,
			cid: 1,
			keywords: "",
			isInvitation: 0,
			loadingType: 0,
			isEnable:'否',
			contentText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			}
		}
	},
	onLoad: function(e) {
		uni.showLoading({
			title: '加载中...'
		});
		let a = this.$queue.getData("isEnable")
		if (a) {
			this.isEnable = a;
		}
		this.cid = e.cid;
		this.keywords = e.name;
		uni.setNavigationBarTitle({
			title: e.name
		});
		this.getUserInfo();
	},
	onPageScroll: function(e) {
		this.scrollTop = e.scrollTop > 200;
	},
	methods: {
		getUserInfo() {
			let userId = this.$queue.getData("userId");
			if (userId) {
				this.$Request.getT("/user/" + userId).then(res => {
					if (res.status === 0) {
						this.$queue.setData("image_url", res.data.image_url);
						this.$queue.setData("mobile", res.data.phone);
						this.isInvitation = res.data.isInvitation;
						this.$queue.setData("isInvitation", res.data.isInvitation);
						this.$queue.setData("relation", res.data.invitation);
						this.$queue.setData("grade", res.data.grade);
						this.$queue.setData("nickName", res.data.nickName);
						this.$queue.setData("relation_id", res.data.relationId);
						this.$queue.setData("gender", parseInt(res.data.gender));
					}
				});
			}
		},
		clearInput() {
			this.keywords = '';
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.position = this.tab[e.currentTarget.dataset.id].position;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
			this.page = 1;
			uni.showLoading({
				title: '加载中...'
			});
			this.loadCouponList("Refresh");






		},
		topScrollTap: function() {
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 300
			});
		},

		deleteKeyword() { //清空最近搜索的关键词
			this.recentKeyword = [];
			this.$queue.remove(this.localKeyword);
		},

		loadCouponList: function(type) {
			console.log("----cid------" + this.cid);
			console.log("----page------" + this.page);
			console.log("----position------" + this.position);
			this.loadingType = 1;
			this.$Request.get(this.$Api.common.couponlist, {
				KeyWords:this.keywords,
				ColumnId: this.cid,
				Position:this.position,
				PageNo: this.page
			}).then(res => {
				this.loadingType = 0;
				if (res.Code === 1) {
					if (this.page === 1) {
						this.couponlist = [];
					}
					res.Message.forEach(d => {
						let grade = this.$queue.getData("grade");
						d.tkmoneys = ((d.itemendprice * (d.tkrates * 0.01)) * 0.7).toFixed(2);
						if (grade) {
							d.tkmoney = ((d.itemendprice * (d.tkrates * 0.01)) * parseFloat(grade)).toFixed(2);
						} else {
							d.tkmoney = ((d.itemendprice * (d.tkrates * 0.01)) * 0.3).toFixed(2);
						}
						d.itemsale = d.itemsale > 10000 ? "已售 " + (d.itemsale / 10000).toFixed(1) + "万" : "已售 " + d.itemsale;
						this.couponlist.push(d);
					})
				} else {
					this.loadingType = 2;
				}
				uni.hideLoading();

				if (type === "Refresh") {
					uni.stopPullDownRefresh(); // 停止刷新
				}
			})
		},
		navigateBack: function() {
			uni.navigateBack();
		},
		toGoodsInfo: function(itemid) {
			let relation_id = this.$queue.getData("relation_id");
			if (relation_id) {
				uni.navigateTo({
					url: "/pages/detail/goodsinfo?itemid=" + itemid + "&relation_id=" + relation_id,
				})
			} else {
				uni.navigateTo({
					url: "/pages/detail/goodsinfo?itemid=" + itemid,
				})
			}
		},
		searchInput: function(e) {

			this.keywords = e.detail.value;

		},
		submitSearch: function() {
			let keywords = this.$queue.get(this.localKeyword);
			if (JSON.stringify(keywords).indexOf(this.keywords) === -1) {
				this.$queue.insert({
					key: this.localKeyword,
					value: {
						keyword: this.keywords
					},
					capacityNum: 20, //队列容量
				});
			}
			uni.showLoading({
				title: '加载中...'
			});
















			this.page = 1;
			this.loadCouponList("Refresh");
			this.topScrollTap();


		},

	},
	onReady: function() {
		this.loadCouponList();
	},
	onReachBottom: function() {
		this.page = this.page + 1;
		this.loadCouponList();
	},
	onPullDownRefresh: function() {
		this.page = 1;
		this.loadCouponList("Refresh");
	}
}
